<template>
    <div class="main">
        <div class="schoolAccount">
            <div class="searchA">
                <el-select v-model="region_id" v-show="isCity" placeholder="请选择区县" clearable size="small">
                    <el-option v-for="item in regionData" :key="item.id" :label="item.region_name" :value="item.id"></el-option>
                </el-select>
                <el-select v-model="school_id" placeholder="请输入学校关键字、拼音" reserve-keyword filterable remote clearable :remote-method="queryMethod" size="small" :loading="isSchoolLoad" @focus="eliminate">
                    <el-option v-for="item in schoolData" :key="item.id" :label="item.school_name" :value="item.id"></el-option>
                </el-select>
                <el-select v-model="school_type" placeholder="学校类型" clearable size="small">
                    <el-option v-for="item in typeData" :key="item.id" :label="item.type_name" :value="item.id"></el-option>
                </el-select>
                <el-select v-model="school_attr" placeholder="学校性质" clearable size="small">
                    <el-option v-for="item in attrData" :key="item.id" :label="item.attr_name" :value="item.id"></el-option>
                </el-select>
                <div style="margin-right:20px;">
                    <el-input v-model="keyword" placeholder="请输入用户关键字、拼音" clearable size="small"></el-input>
                </div>
                <div style="margin-right:20px;">
                    <el-button size="small" type="primary" plain icon="el-icon-search" @click="getList(1)">查询</el-button>
                </div>
                <el-button size="small" type="primary" icon="el-icon-download" @click="importTemplate" style="margin-right:10px;" v-if="method_auth.importData">
                    <!-- <i class="el-icon-download"></i> -->
                    导入模板下载
                </el-button>
                <el-button size="small" type="primary" icon="el-icon-upload2" @click="handleImport" style="margin-right:10px;" v-if="method_auth.importData">
                    <!-- <i class="el-icon-upload"></i> -->
                    excel导入
                </el-button>
                <el-popover placement="bottom" title width="200" trigger="click" v-model="importError" v-if="method_auth.importData">
                    <div>
                        <ul class="popoverA">
                            <li v-if="importErrorList.length" style="color:red">共{{importErrorList.length-1}}条错误信息</li>
                            <li v-else>暂无错误信息</li>
                            <li v-for="(item, index) in importErrorList" :key="index">{{item}}</li>
                        </ul>
                    </div>
                    <el-button size="small" type="primary" slot="reference" style="margin-left:10px" icon="el-icon-document" @click="getImportError">导入结果</el-button>
                </el-popover>
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" :disabled="!method_auth.actAdd ? true : false" @click="addBtn">新增</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" style="width: 100%" size="medium">
                    <el-table-column prop="id" label="编号" width="100" />
                    <el-table-column prop="region_name" label="区县" v-if="isCity == true" width="100" />
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" v-if="item.title == '状态'" width="90">
                            <template slot-scope="scope">
                                <el-switch v-model="tableData[scope.$index][item.prop]" @change="
                                switchChange(
                                  scope.row.id,
                                  item.prop,
                                  tableData[scope.$index][item.prop],
                                  scope.$index
                                )
                              " :disabled="!method_auth.setState"></el-switch>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :min-width="item.minwidth" v-else :show-overflow-tooltip="true"></el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="150">
                        <template slot-scope="scope">
                            <el-button @click="editBtn(scope.row)" type="text" size="small" v-if="method_auth.actEdit">编辑</el-button>
                            <el-button @click="handleDelete(scope.row)" type="text" size="small" style="color: #ff2b2b" v-if="method_auth.actDelete">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <el-pagination background :current-page.sync="curr" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes, total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
            <!-- 新增编辑弹窗 -->
            <el-dialog class="dialogStyle" v-dialogDrag :title="title" :visible.sync="dialogVisible" width="500px" v-if="dialogVisible" :close-on-click-modal="false" @close="handleClose">
                <el-form ref="dataForm" :rules="rules" :model="dataForm" status-icon label-position="top" label-width="100px" :inline="true">
                    <el-form-item label="登录账号" prop="user_name">
                        <el-input v-model="dataForm.user_name" placeholder="请输入登录手机号码" size="small" maxlength="11"></el-input>
                    </el-form-item>
                    <el-form-item label="姓名" prop="real_name">
                        <el-input v-model="dataForm.real_name" placeholder="请输入姓名" size="small" />
                    </el-form-item>
                    <el-form-item label="手机号">
                        <el-input v-model="dataForm.mobile" placeholder="请输入手机号" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password" v-if="title == '新增'">
                        <el-input v-model="dataForm.password" placeholder="请输入密码" size="small" type="password" />
                    </el-form-item>
                    <el-form-item label="密码" prop="password" v-else>
                        <el-input v-model="dataForm.password" placeholder="无需修改请留空" size="small" type="password" />
                    </el-form-item>
                    <el-form-item label="区县" prop="region_id" v-if="isCity == true && title == '新增'">
                        <el-select v-model="dataForm.region_id" placeholder="请选择" size="small">
                            <el-option v-for="item in regionData" :key="item.id" :label="item.region_name" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="学校" prop="school_id" v-if="title == '新增'">
                        <el-select v-model="dataForm.school_id" placeholder="请输入学校关键词" reserve-keyword filterable remote clearable :remote-method="remoteMethod" size="small" @focus="getSchool" :loading="isSchoolLoad" @change="schoolChange">
                            <el-option v-for="item in schoolFromData" :key="item.id" :label="item.school_name" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="负责人权限">
                                <el-switch v-model="dataForm.main_account"></el-switch>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="状态">
                                <el-switch v-model="dataForm.disabled" :disabled="!method_auth.setState"></el-switch>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <div class="dialogInput">
                        <div class="padd">
                            <div class="paddTit">资源权限</div>
                            <div class="tree_main">
                                <el-form-item style="display: block;">
                                    <el-tree :data="nodeData" show-checkbox check-on-click-node node-key="id" ref="tree" default-expand-all :props="defaultProps">
                                    </el-tree>
                                </el-form-item>

                            </div>
                        </div>
                    </div>

                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button type="primary" class="btn" @click="createData" v-if="title == '新增'">确 定</el-button>
                    <el-button type="primary" class="btn" @click="updateData()" v-else>确 定</el-button>
                </div>
            </el-dialog>
            <!-- 用户导入对话框 -->
            <el-dialog :title="upload.title" v-dialogDrag :visible.sync="upload.open" width="400px" append-to-body>
                <el-upload ref="upload" :data="formData" :limit="1" accept=".xlsx, .xls, .csv" :headers="upload.headers" :action="upload.url" :disabled="upload.isUploading" :on-progress="handleFileUploadProgress" :on-success="handleFileSuccess" :on-error='handleFileError' :auto-upload="false" :before-upload="beforeUpload" drag>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">
                        将文件拖到此处，或
                        <em>点击上传</em>
                    </div>
                    <div class="el-upload__tip" slot="tip">
                        <el-link type="info" style="font-size:12px" @click="importTemplate">下载模板</el-link>
                    </div>
                    <div class="el-upload__tip" style="color:red" slot="tip">提示：仅允许导入xls/xlsx格式文件！</div>
                </el-upload>
                <div slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="submitFileForm">确 定</el-button>
                    <el-button @click="upload.open = false">取 消</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    hashApi,
    schoolManageListApi,
    schoolManageAddApi,
    schoolManageEditApi,
    schoolManageDeleteApi,
    schoolManageInfoApi,
    schoolManageQueryApi,
    schoolManageViewApi,
    schoolManageSetApi,
    schoolManageImportApi,
    schoolManageErrApi
} from '@/api/index';
export default {
    data() {
        return {
            total: 0,
            curr: 1,
            pageSize: 15,
            isLoading: false,
            isSchoolLoad: false,
            isCity: false, //是否市级账号
            isRegionId: '',
            region_id: '',
            school_id: '',
            school_type: '',
            school_attr: '',
            keyword: '',
            regionData: [],
            schoolData: [],
            schoolFromData: [],
            typeData: [],
            attrData: [],
            hash: '',
            tableHeight: window.innerHeight - 180,
            importErrorList: [],
            importError: false,
            loading: false,
            method_auth: {
                actAdd: 0,
                actEdit: 0,
                actDelete: 0,
                setState: 0,
                importData: 0
            },
            title: '',
            dialogVisible: false,
            tableHead: [
                { title: '登录账号', prop: 'user_name', minwidth: '150' },
                { title: '姓名', prop: 'real_name', minwidth: '100' },
                { title: '手机号', prop: 'mobile', minwidth: '150' },
                { title: '学校名称', prop: 'school_name', minwidth: '400' },
                { title: '学校性质', prop: 'school_attr', minwidth: '100' },
                { title: '学校类型', prop: 'school_type', minwidth: '100' },
                { title: '状态', prop: 'disabled' }
            ],
            // 表单参数
            dataForm: {
                id: 0,
                user_name: '', // 用户名
                password: '', // 密码
                real_name: '', // 真实姓名
                mobile: '', // 手机号码
                main_account: true, // 是否主账号
                disabled: true, // 启动0，禁用1
                region_id: '',
                school_id: ''
            },
            // 表单校验
            rules: {
                user_name: [
                    { required: true, message: '请输入登录手机号码', trigger: 'blur' },
                    {
                        required: true,
                        pattern: /^1\d{10}$/,
                        message: '手机号格式错误',
                        trigger: 'blur'
                    }
                ],
                real_name: [
                    { required: true, message: '请输入真实姓名', trigger: 'blur' }
                ],
                mobile: [
                    { required: true, message: '请输入手机号', trigger: 'blur' },
                    {
                        required: true,
                        pattern: /^1\d{10}$/,
                        message: '手机号格式错误',
                        trigger: 'blur'
                    }
                ],
                school_id: [
                    { required: true, message: '请选择学校', trigger: 'change' }
                ],
                password: [{ min: 8, message: '密码必须大于8位', trigger: 'blur' }],
                region_id: [
                    { required: true, message: '请选择区县', trigger: 'change' }
                ]
            },
            tableData: [],
            nodeData: [],
            infoData: [],
            hasNodes: [],
            defaultProps: {
                children: 'children',
                label: 'node_name',
                disabled: 'defaulted',
            },
            // 用户导入参数
            upload: {
                // 是否显示弹出层（用户导入）
                open: false,
                // 弹出层标题（用户导入）
                title: '',
                // 是否禁用上传
                isUploading: false,
                // 设置上传的请求头部
                headers: { token: localStorage.getItem('token') },
                // 上传的地址
                url: schoolManageImportApi
            },
            // 额外携带的参数
            formData: {
                // file: this.file
            },
            file: '',
            schoolAttr: '',
            schoolType: '',
            schoolChangeType: false
        };
    },
    mounted() {
        this.getList();
        this.getResource();
        this.getHash();
    },
    watch: {
        total() {
            if (
                this.total === (this.pageNum - 1) * this.pageSize &&
                this.total !== 0
            ) {
                this.pageNum -= 1;
                this.getList();
            }
        }
    },
    methods: {
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.curr = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.curr = newPage;
            this.getList();
        },
        // 导入模板下载
        importTemplate() {
            window.location.href =
                process.env.TEMPLATE_URL +
                '/static/template/schooltemplate.xls?fileName=' +
                encodeURI('schooltemplate.xls');
        },
        // 导入按钮操作
        handleImport() {
            this.upload.title = '学校账号导入';
            this.upload.open = true;
        },
        // 上传文件之前的钩子
        beforeUpload(file) {
            this.file = file.name;
        },
        // 文件上传中处理
        handleFileUploadProgress(event, file, fileList) {
            this.upload.isUploading = true;
            // 打开加载层
            this.loading = this.$loading({
                lock: true,
                text: '正在导入...',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.3)'
            });
        },
        // 文件上传成功处理
        handleFileSuccess(response, file, fileList) {
            this.loading.close();
            this.getList();
            this.upload.open = false;
            this.upload.isUploading = false;
            this.$refs.upload.clearFiles();
            if (response.code == 1) {
                if (response.token && response.token !== '') {
                    //覆盖localstorage中token
                    localStorage.setItem('token', response.token);
                }
                this.$alert('导入完成，请到[导入结果]查看记录', '导入结果', {
                    dangerouslyUseHTMLString: true
                });
                this.getList();
            } else {
                this.$alert(response.msg, '导入失败', {
                    dangerouslyUseHTMLString: true
                });
            }
        },
        //文件上传失败
        handleFileError(res, file) {
            this.loading.close();
            this.$message({
                showClose: true,
                message: res.msg,
                type: "error"
            });
        },
        // 提交上传文件
        submitFileForm() {
            this.$refs.upload.submit();
        },
        // 导入错误信息
        async getImportError() {
            if (!this.importError) {
                const res = await this.axios.post(schoolManageErrApi);
                if (res.data.code == 1) {
                    if (res.data.data) {
                        this.importErrorList = res.data.data;
                    }
                }
            }
        },
        // 切换表格状态开关
        async switchChange(id, key, val, idx) {
            let vals = val == false ? 1 : 0;
            var url = schoolManageSetApi;
            let datas = {
                id: id,
                state: vals
            };
            const res = await this.axios.post(url, datas);
            if (res.data.code == 1) {
            } else {
                this.tableData[idx][key] = !val;
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        eliminate() {
            this.schoolData = [];
        },
        schoolChange(e) {
            if (e) {
                this.schoolFromData.map(item => {
                    if (item.id == e) {
                        this.schoolAttr = item.school_attr;
                        this.schoolType = item.school_type;
                    }
                })
                this.schoolChangeData = {
                    school_type: this.schoolType,
                    school_attr: this.schoolAttr
                }
                this.schoolChangeType = true;
                this.getNodeData(this.schoolChangeData);
            } else {
                this.schoolChangeType = false;
                this.getNodeData()
            }
        },
        //获取学校列表
        getSchool() {
            this.schoolFromData = [];
            this.remoteMethod();
        },
        //搜索栏学校列表查询
        async queryMethod(query) {
            if (query !== '') {
                let submit_id = this.isRegionId;
                if (this.isCity) {
                    submit_id = this.region_id;
                }
                let datas = {
                    region_id: submit_id,
                    keyword: query
                };
                this.isSchoolLoad = true;
                const res = await this.axios.post(schoolManageQueryApi, datas);
                if (res.data.code == 1) {
                    this.schoolData = res.data.data;
                } else {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: 'error'
                    });
                }
                this.isSchoolLoad = false;
            }
        },
        // 表单学校列表查询
        async remoteMethod(query) {
            let submit_id = this.isRegionId;
            if (this.isCity) {
                submit_id = this.dataForm.region_id;
            }
            if (!submit_id) {
                this.$message({
                    showClose: true,
                    message: '请选择区县',
                    type: 'error'
                });
                return;
            }
            this.isSchoolLoad = true;
            let datas = {
                region_id: submit_id,
                keyword: query
            };
            const res = await this.axios.post(schoolManageQueryApi, datas);
            if (res.data.code == 1) {
                this.schoolFromData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.isSchoolLoad = false;
        },
        //获取节点列表
        async getNodeData(e) {
            this.nodeData = [];
            let datas = {}
            if (e) {
                datas = {
                    school_attr: e.school_attr, // 学校性质
                    school_type: e.school_type // 学校类型
                };
            }
            const res = await this.axios.post(schoolManageViewApi, datas)
            if (res.data.code == 1) {
                if (this.hasNodes && this.title == '编辑') {
                    this.nodeData = res.data.data.nodes;
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys([]);
                        this.hasNodes.forEach((i) => {
                            var node = this.$refs.tree.getNode(i);
                            if (node && node.isLeaf) {
                                this.$refs.tree.setChecked(node, true);
                            }
                        });
                    })
                } else if (this.schoolChangeType && this.title == '新增') {
                    this.nodeData = res.data.data.nodes;
                    // 获取所有id 赋值树节点
                    const arr = [];
                    this.nodeData.forEach(data => {
                        arr.push(data.id)
                        if (data.children) {
                            data.children.forEach(item => {
                                arr.push(item.id)
                            })
                        }
                    });
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys(arr);
                    })
                } else {
                    this.nodeData = []
                }

            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        //获取账号信息
        async getInfoData(id) {
            const datas = {
                id: id
            };
            const res = await this.axios.post(schoolManageInfoApi, datas);
            if (res.data.code == 1) {
                this.dataForm.id = res.data.data.id;
                this.dataForm.user_name = res.data.data.user_name;
                this.dataForm.real_name = res.data.data.real_name;
                this.dataForm.mobile = res.data.data.mobile;
                this.dataForm.password = '';
                this.dataForm.region_id = res.data.data.region_id;
                this.hasNodes = res.data.data.nodes;
                // this.getNodeData(this.hasNodes);
                this.getNodeData(res.data.data);
                this.schoolFromData = [
                    {
                        id: res.data.data.school_id,
                        school_name: res.data.data.school_name
                    }
                ];
                this.dataForm.school_id = res.data.data.school_id;
                if (res.data.data.disabled == 1) {
                    this.dataForm.disabled = false;
                } else {
                    this.dataForm.disabled = true;
                }
                if (res.data.data.main_account == 1) {
                    this.dataForm.main_account = true;
                } else {
                    this.dataForm.main_account = false;
                }
                // if (res.data.data.main_account == 1) {
                //     this.formData.disabled = false;
                // } else {
                //     this.formData.disabled = true;
                // }
                // if (res.data.data.main_account == 1) {
                //     this.formData.main_account = true;
                // } else {
                //     this.formData.main_account = false;
                // }
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //获取学校账号列表
        async getList(e) {
            if (e) {
                this.curr = 1;
            }
            this.isLoading = true;
            const res = await this.axios.post(schoolManageListApi, {
                curr: this.curr,
                pagesize: this.pageSize,
                region_id: this.region_id,
                school_id: this.school_id,
                school_attr: this.school_attr,
                school_type: this.school_type,
                keyword: this.keyword
            });
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
                let resources = res.data.data.resources.method_auth;
                var obj = this.method_auth;
                this.isCity = res.data.data.resources.grade_auth.grade_status;
                this.main_auth = res.data.data.resources.main_auth.main_account;
                resources.map(function (value) {
                    if (value.method_name) {
                        obj[value.method_name] = value.exist;
                    }
                });
                this.method_auth = obj;
                this.isLoading = false;
            } else {
                this.isLoading = false;
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //重置对话框表单
        resetForm() {
            this.dataForm = {
                id: 0,
                user_name: '', // 用户名
                password: '', // 密码
                real_name: '', // 真实姓名
                mobile: '', // 手机号码
                main_account: true,
                disabled: true,
                region_id: '',
                school_id: ''
            };
            this.nodeData = [];
            this.hasNodes = [];
            this.schoolFromData = [];
            this.$nextTick(() => {
                this.$refs['dataForm'].clearValidate();
            });
        },
        // 点击新增
        addBtn() {
            this.title = '新增';
            this.dialogVisible = true;
            this.resetForm();
            this.schoolChangeType = false
            this.getNodeData();//获取节点列表
            this.getHash();
        },
        // 学校账号增加
        createData() {
            this.$refs['dataForm'].validate(async valid => {
                if (valid) {
                    if (this.dataForm.main_account == true) {
                        var main_account = 1;
                    } else {
                        var main_account = 0;
                    }
                    if (this.dataForm.disabled == true) {
                        var disabled = 0;
                    } else {
                        var disabled = 1;
                    }
                    let datas = {
                        user_name: this.dataForm.user_name, // 用户名
                        password: this.dataForm.password, // 密码
                        real_name: this.dataForm.real_name, // 真实姓名
                        mobile: this.dataForm.mobile,
                        main_account: main_account, // 是否主账号
                        disabled: disabled, // 启动0，禁用1
                        region_id: this.dataForm.region_id, // 区县id
                        school_id: this.dataForm.school_id, // 学校id
                        node_ids: this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys()).join(','),
                        hash: this.hash
                    };
                    if (!this.isCity) {
                        datas.region_id = this.isRegionId;
                    }
                    const res = await this.axios.post(schoolManageAddApi, datas);
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.getList();
                        this.$message({
                            message: res.data.msg,
                            type: 'success'
                        });
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getHash();
                }
            });
        },
        // 学校账号编辑
        async updateData() {
            this.$refs['dataForm'].validate(async valid => {
                if (valid) {
                    if (this.dataForm.main_account == true) {
                        var main_account = 1;
                    } else {
                        var main_account = 0;
                    }
                    if (this.dataForm.disabled == true) {
                        var disabled = 0;
                    } else {
                        var disabled = 1;
                    }

                    let datas = {
                        id: this.dataForm.id,
                        user_name: this.dataForm.user_name, // 用户名
                        password: this.dataForm.password, // 密码
                        real_name: this.dataForm.real_name, // 真实姓名
                        mobile: this.dataForm.mobile,
                        main_account: main_account, // 是否主账号
                        disabled: disabled, // 启动0，禁用1
                        node_ids: this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys()).join(','),
                        hash: this.hash
                    };
                    if (!this.isCity) {
                        datas.region_id = this.isRegionId;
                    }
                    const res = await this.axios.post(schoolManageEditApi, datas);
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.getList();
                        this.$message({
                            message: res.data.msg,
                            type: 'success'
                        });
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getHash();
                }
            });
        },

        // 点击编辑
        editBtn(row) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.$nextTick(() => {
                this.resetForm();
                // this.$refs['dataForm'].clearValidate();
            });
            this.getInfoData(row.id);
            this.getHash();
        },

        //删除
        handleDelete(row) {
            this.$confirm('是否确认删除账号"' + row.user_name + '"?', '警告', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(async () => {
                    const datas = {
                        id: row.id,
                        deleted: 1
                    };
                    const res = await this.axios
                        .post(schoolManageDeleteApi, datas)
                        .then(res => {
                            if (res.data.code == 1) {
                                this.$notify.success({
                                    title: '成功',
                                    message: '删除成功'
                                });
                                if ((this.total - 1) % this.pageSize === 0) {
                                    this.curr -= 1;
                                }
                                this.getList();
                            } else {
                                this.$notify.error({
                                    title: '失败',
                                    message: res.data.msg
                                });
                            }
                        });
                })
                .catch(res => {
                    this.$notify.error({
                        title: '失败',
                        message: res.data.msg
                    });
                });
        },
        //获取资源列表
        async getResource() {
            const res = await this.axios.post(schoolManageViewApi);
            if (res.data.code == 1) {
                //区县列表
                this.regionData = res.data.data.region;
                this.typeData = res.data.data.school_type;
                this.attrData = res.data.data.school_attr;
                this.isRegionId = res.data.data.region_id;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //获取hash
        async getHash() {
            const datas = {
                page_hash: 'schoolAccount'
            };
            const res = await this.axios.post(hashApi, datas);
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //关闭弹窗
        handleClose() {
            this.dialogVisible = false;
        }
    }
};
</script>

<style lang="scss" scoped>
.schoolAccount {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        /deep/ .el-input__inner {
            width: 150px;
        }
        .el-select {
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
            margin-left: 10px;
        }
    }
}
// 弹窗
/deep/ .dialogStyle {
    .el-form-item {
        margin-bottom: 20px !important;
    }

    .el-form-item__label {
        line-height: 30px;
    }

    .el-form--label-top .el-form-item__label {
        padding: 0;
    }

    .el-dialog__body {
        padding: 30px 15px 10px 30px;
    }

    .el-input__inner {
        width: 213px;
    }
}
.dialogInput {
    .tree_main {
        border: 1px solid #dcdfe6;
        padding: 15px;
        max-height: 250px;
        overflow-y: auto;
        margin-right: 10px;
    }
    .padd {
        width: 100%;
        margin-bottom: 10px;
        .paddTit {
            margin-bottom: 10px;
        }

        .paddTit::before {
            content: '*';
            color: #f56c6c;
            margin-right: 4px;
        }
    }
}
</style>



